import styled from "styled-components"

export const Container = styled.div`
	.wrapper-content {
		display: flex;
		align-items: center;
		p {
			color: #444;
			font-family: "Montserrat", ans-serif;
			font-size: 16px;
			font-weight: 300;
			line-height: 1.6em;
		}
	}

	.wraper-cta-left {
		padding-left: 32px;
		position: relative;

		img {
			border-radius: 4px;
			width: 100%;
		}

		a.button-cta {
			font-family: "Poppins", Sans-serif;
			font-size: 18px;
			font-weight: 600;
			color: #fff;
			background-color: #34e67f;
			border-radius: 5px;
			padding: 25px 50px 25px 50px;
			display: inline-block;
			position: relative;
			bottom: 30px;
			right: 24px;
		}
	}
`
